import { Component, h, State, Prop, Host, Event, EventEmitter } from '@stencil/core';
import { joinClass } from '../../../utils/joinClass';

export interface DropdownOption {
  label: string;
  value: string;
}

@Component({
  tag: 'charma-dropdown',
  styleUrl: 'charma-dropdown.scss',
  shadow: true,
})
export class CharmaDropdown {
  @State() selectedOption: DropdownOption = { label: '', value: '' };
  @State() isOpen = false;
  @Prop() options: DropdownOption[] = [];
  @Prop() label: string = 'Options';
  @Prop() required = false;
  @Prop() error = false;
  @Prop() name: string;
  @Prop() defaultValue: DropdownOption;
  @Event() valueChange: EventEmitter<string>;

  componentWillLoad() {
    if (this.defaultValue) {
      this.selectedOption = this.defaultValue;
    }
  }

  onSetValue = (value: DropdownOption) => {
    this.selectedOption = value;
    this.isOpen = false;
    this.valueChange.emit(value.value);
  };

  toggleDropdown = () => {
    this.isOpen = !this.isOpen;
  };

  render() {
    const dropdownMenu = (
      <ul
        role="listbox"
        class={joinClass('absolute  drop-shadow-sm  flex flex-col py-2 px-1 w-full top-[3.6rem] rounded-md border border-input z-50 overflow-scroll max-h-[150px] bg-white ')}
      >
        {this.options.map(option => (
          <li
            class="font-ns  hover:bg-[#e2e2e2] px-[15px] py-1 text-sm cursor-pointer rounded-sm "
            onClick={e => {
              e.stopPropagation();
              this.onSetValue(option);
            }}
          >
            {option.label}
          </li>
        ))}
      </ul>
    );
    return (
      <Host>
        <div class="grid gap-2 relative">
          <label class="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70 font-ns capitalize" htmlFor="month">
            {this.label} {this.required && <span class="text-red-500">*</span>}
          </label>
          <button
            type="button"
            role="combobox"
            aria-controls="radix-:R2ckpmmkr9hja:"
            aria-expanded="false"
            aria-autocomplete="none"
            dir="ltr"
            data-state="closed"
            data-placeholder=""
            class="flex h-9 w-full items-center font-ns justify-between rounded-md border border-input bg-transparent px-3 py-2 text-sm shadow-sm ring-offset-background placeholder:text-muted-foreground focus:outline-none focus:ring-1 focus:ring-ring disabled:cursor-not-allowed disabled:opacity-50"
            id="month"
            onClick={this.toggleDropdown}
          >
            <span>
              <span>{this.selectedOption.label}</span>
            </span>
            <svg width="15" height="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg" class="h-4 w-4 opacity-50" aria-hidden="true">
              <path
                d="M4.93179 5.43179C4.75605 5.60753 4.75605 5.89245 4.93179 6.06819C5.10753 6.24392 5.39245 6.24392 5.56819 6.06819L7.49999 4.13638L9.43179 6.06819C9.60753 6.24392 9.89245 6.24392 10.0682 6.06819C10.2439 5.89245 10.2439 5.60753 10.0682 5.43179L7.81819 3.18179C7.73379 3.0974 7.61933 3.04999 7.49999 3.04999C7.38064 3.04999 7.26618 3.0974 7.18179 3.18179L4.93179 5.43179ZM10.0682 9.56819C10.2439 9.39245 10.2439 9.10753 10.0682 8.93179C9.89245 8.75606 9.60753 8.75606 9.43179 8.93179L7.49999 10.8636L5.56819 8.93179C5.39245 8.75606 5.10753 8.75606 4.93179 8.93179C4.75605 9.10753 4.75605 9.39245 4.93179 9.56819L7.18179 11.8182C7.35753 11.9939 7.64245 11.9939 7.81819 11.8182L10.0682 9.56819Z"
                fill="currentColor"
                fill-rule="evenodd"
                clip-rule="evenodd"
              ></path>
            </svg>
          </button>

          {this.isOpen && dropdownMenu}
        </div>
      </Host>
    );
  }
}
